.App {
  width: 100%;
  height:100%;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

html, body {
    color:  #ffffff;
    background: -webkit-gradient(linear, left top, left bottom, from(#00cc99), to(#ff9966));
    background: -webkit-linear-gradient(top, #00cc99 0%, #ff9966 100%);
    background: -o-linear-gradient(top, #00cc99 0%, #ff9966 100%);
    background: linear-gradient(to bottom, #00cc99 0%, #ff9966 100%);
}


body {
  font-family: "Helvetica Neue", Helvetica, sans-serif;

  font-weight: 200;
  margin: 0 auto 50px;
}

h1 {
  text-align: center
}


div, button {
  margin: 10px;
}


.Col {
  border-style: outset;
  border-color: gray;
  border-width: 2px;
  background: lightgray;
}


.cameraOutput {
    width: 500px;
    height: 375px;
    background-color: #666;
}


textarea {
  font-family: monospace;
  font-size: inherit;
  height: 150px;
  width: 100%;
}



.webcams > video:nth-of-type(1) {
  -webkit-filter: grayscale(1);
  filter: grayscale(1);
}
.webcams > video:nth-of-type(2) {
  -webkit-filter: sepia(1);
  filter: sepia(1);
}
.webcams > video:nth-of-type(3) {
  -webkit-filter: saturate(8);
  filter: saturate(8);
}
.webcams > video:nth-of-type(4) {
  -webkit-filter: hue-rotate(90deg);
  filter: hue-rotate(90deg);
}
.webcams > video:nth-of-type(6) {
  -webkit-filter: invert(1);
  filter: invert(1);
}
.webcams > video:nth-of-type(7) {
  -webkit-filter: brightness(3);
  filter: brightness(3);
}
.webcams > video:nth-of-type(8) {
  -webkit-filter: contrast(4);
  filter: contrast(4);
}
.webcams > video:nth-of-type(9) {
  -webkit-filter: blur(1px);
  filter: blur(1px);
}



